body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}
.header{
  background-color: black;
  color:white;
  padding:20px 30px;
  display:flex;
  justify-content: space-between;
  font-size:30px;
  font-family:Arial, Helvetica, sans-serif;
  letter-spacing:2px ;
}
.cart-icon .counter{
  height:20px;
  width:18px;
  background-color: yellow;
  border-radius: 50%;
  display:flex;
  position:relative;
  display: flex;
  text-align: center;
  align-items: center;
  justify-content: center;
  right:7px;
  top:-6px;
  font-size:12px;
  color: black;
}
.cart-icon{
  display: flex;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}
img{
  height: 150px;
}
.cart-wrapper{
  display: inline-flex;
  margin: 40px;
  box-shadow: 0px 0px 7px black;
  width: 320px;
  border-radius: 5px;
  
}
.image-wrapper{
  width:130px;
  display: flex;
  align-items: center;
  justify-content: center;
  padding: 10px;
  border-right: 1px solid rgb(116, 98, 98);
}
.content{
  display:flex;
  flex-direction: column;
  flex-grow:1;
  gap:10px;
  padding: 10px;
}
.price{
  margin-bottom: auto;
}
.content button{
  background-color: black;
  color:aliceblue;
  border-radius:3px;

}
.quantity{
  display: flex;
  align-self: flex-end;
  border-radius: 3px;
  border:1px solid grey;
  
}
.quantity div{
  height:22px;
  width:22px;
  display: flex;
  align-items: center;
  justify-content: center;
}
.quantity > div:first-child, .quantity >button{
  background-color: rgba(11, 11, 120, 0.938);
  color: aliceblue;
}

.quantity > button:disabled{
    opacity:0.7;
  }
  main{
    padding:20px;
    display: flex;
    flex-wrap: wrap;
    min-height: 80vh;
    justify-content: center;
    align-items: center;
    gap:50px;
  }
  .total{
    box-sizing: border-box;
    position: fixed;
    background-color: aqua;
    padding: 17px 30px;
    bottom: 0;
    width: 100%;
    text-align: end;
    color: black;
  }